$main-width: 625px;

html, body {
  height: 100%;
  width: 100%;
  background-color: black;
  color: white;
  touch-action: none;
}

body {
  margin: 0;
  font-family: 'M PLUS Rounded 1c', sans-serif;
  font-size: 24px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

div {
  box-sizing: border-box;
}

.title {
  text-align: center;
}

.title-left {
  text-align: left;
  margin-left: 2em;
}

.title-right {
  text-align: right;
  margin-right: 2em;
}

.main {
  position: relative;
  width: $main-width;
  height: 100vh;
  max-height: 100vh;
  margin: 0 auto;
  overflow: hidden;
}

.scroller {
  position: fixed;
  left: 0;
  top: 0;
  height: 100vh;
  width: 100vw;
  z-index: 1000;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.parallax {
  position: absolute;
  width: $main-width;
  transform: translate(0, -50%);
}

.fade {
  position: absolute;
  width: $main-width;
  transform: translate(0, -50%);
}

.pixel-row {
  display: block;
  position: relative;
  left: 50%;
  transform: translate(-50%, 0);
  margin: 0;
  padding: 0;
  width: fit-content;
  white-space: nowrap;
}

.mosaic-wrapper {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

img.mosaic {
  position: relative;
  image-rendering: pixelated;
  image-rendering: -moz-crisp-edges;
  image-rendering: crisp-edges;
  z-index: 100;
}